  "editable-field.institution-manager-interface.ts": "interface InstitutionsManager {\n    reloadList?: (page?: number) => void;\n    addInstitution: (institution: Institution) => void;\n    removeInstitution: (institution: Institution) => void;\n    affiliatedList: Institution[];\n}",
  "editable-field.institutions.hbs": "        <EditableField\n            data-analytics-scope='Handbook'\n            @managerComponent={{component 'editable-field/institutions-manager' node=this.registration}}\n            @registration={{this.registration}}\n            @title={{t 'registries.registration_metadata.affiliated_institutions'}}\n            @name='affiliated institutions'\n            as |field|\n        >\n            <field.edit>\n                <InstitutionSelectList\n                    @user={{this.currentUser.user}}\n                    @node={{this.registration}}\n                    @manager={{field.manager}}\n                />\n            </field.edit>\n            <field.display>\n                <InstitutionsList\n                    @reloadList={{action (mut field.manager.reloadList)}}\n                    @node={{this.registration}}\n                />\n            </field.display>\n        </EditableField>",
